import React from "react";
import Link from "gatsby-link";

const ListLink = props =>
  <li>
    <Link to={props.to}>
      {props.children}
    </Link>
  </li>


export default () => (
  <div>

      <h2>Articles</h2>

      <ul>
        <ListLink to="/articles/managing-data-science-projects">Managing data science projects</ListLink>
        <ListLink to="/articles/data-science-roi">Data science ROI</ListLink>
      </ul>

  </div>
 );




